/**
AVATARS STYLING
 */
// Give fields padding
.redux-container {
    .redux_field_th {
        margin-top: 16px;
    }

    .redux-container-raw,
    .redux-container-image_select,
    .redux-container-gallery {
        margin-top: 16px;
    }
}

//// Make avatars appear in a row and rounded.
#zipline_smart_avatars-display-theme-one,
#zipline_smart_avatars-display-theme-two {
    display: flex;
    flex-wrap: wrap;
    width: 500px;
}

#zipline_smart_avatars-display-theme-two,
#zipline_smart_avatars-display-theme-one {
    .avatar-wrap {

        img {
            max-height: 100px;
            max-width: 100px;
            padding-right: 10px;
            border-radius: 50%;
        }
    }
}

#zipline_smart_avatars-create-gallery,
#zipline_smart_avatars-create-cover-image-gallery,
#zipline_smart_avatars-create-group-avatar-gallery,
#zipline_smart_avatars-create-group-cover-image-gallery {
    .screenshot {
        max-width: 500px;

        img {
            height: 100px !important;
            width: 100px !important;
            margin-right: 10px;
            border-radius: 50%;
        }
    }
}

// Confirm clear gallery modal
#confirm-modal {
    position: fixed;
    left:0;
    top:0;
    width:100vw;
    height:100vh;
    z-index: 9998;
    display:flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
}

#confirm-modal-inner {
    background-color: white;
    width: 220px;
    height: 120px;
    border: 1px solid #2271b1;
    border-radius: 5px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 50%);
    z-index: 100;
    position: fixed;
    top: 50%;
    left: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;

    .button-wrap {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    button {
        width: 100px;
    }
}

/**
COVER IMAGES STYLING
 */
#zipline_smart_avatars-choose-default-cover-image,
#zipline_smart_avatars-choose-default-group-avatar,
#zipline_smart_avatars-choose-default-group-cover-image {

    .redux-table-container {
        ul li {
            label {
                img {
                    height: 100px !important;
                    width: 100px !important;
                    margin-right: 10px;
                    border-radius: 50%;
                }
            }
        }
    }
}

/**
OVERLAYS STYLING
*/
.redux-container {
    .overlay {
        background-color: white;
        opacity: .3;
        z-index: 2;
        pointer-events: none;
    }

    .allow-interaction {
        opacity: 1 !important;
        pointer-events: all !important;
    }
}
